import React, { useState } from 'react';
import { Box, Button, InputLabel, TextField, Typography } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const CreateBlog = () => {
    const id = localStorage.getItem('userId')
    const navigate = useNavigate();
    const [inputs, setInputs] = useState({
        title: '',
        discription: '',
        image: '',
    })
    //Input Change handleChange
    const handleChange = (e) => {
        setInputs(prevState => ({
            ...prevState,
            [e.target.name]: e.target.value
        })
        )
    };
    //Form
    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const { data } = await axios.post('/api/v1/blog/create-blog', {
                title: inputs.title,
                discription: inputs.discription,
                image: inputs.image,
                user: id
            })
            if (data?.success) {
                alert('Blog Created Successfully')
                navigate('/my-blogs')
            }
        } catch (error) {
            console.log(error);
        }
    }
    return (
        <>
            <form onSubmit={handleSubmit}>
                <Box width={'50%'}
                    border={1}
                    borderRadius={5}
                    padding={1}
                    margin='auto'
                    boxShadow={'10px 10px 20px #ccc'}
                    display='flex'
                    flexDirection='column'
                    marginTop={3}>
                    <Typography variant='h3'
                        textAlign='center'
                        fontWeight='bold'
                        padding={1}
                        color='gray'>
                        Create A Post
                    </Typography>
                    <InputLabel sx={{
                        mb: 1,
                        mt: 2,
                        fontSize: '25px',
                        fontWeight: 'bold',
                    }}> Title
                    </InputLabel>
                    <TextField
                        value={inputs.title}
                        name='title'
                        onChange={handleChange}
                        marginTop='normal'
                        variant='outlined'
                        placeholder='title'
                        required
                    ></TextField>
                    <InputLabel sx={{
                        mb: 1,
                        mt: 2,
                        fontSize: '25px',
                        fontWeight: 'bold',
                    }}> Discription
                    </InputLabel>
                    <TextField
                        value={inputs.discription}
                        name='discription'
                        onChange={handleChange}
                        marginTop='normal'
                        variant='outlined'
                        placeholder='discription'
                        required
                    ></TextField>
                    <InputLabel sx={{
                        mb: 1,
                        mt: 2,
                        fontSize: '25px',
                        fontWeight: 'bold',
                    }}> Image URL
                    </InputLabel>
                    <TextField
                        value={inputs.image}
                        name='image'
                        onChange={handleChange}
                        marginTop='normal'
                        variant='outlined'
                        placeholder='image'
                        required
                    ></TextField>
                    <Button type='submit' sx={{ mt: 2 }} color='primary' variant='contained'>Submit</Button>
                </Box>
            </form>
        </>
    )
}

export default CreateBlog